.bg-full{
    background-size: cover;
    background-position: 50% 50%;
    position: relative;
}
.bg-full *{
    z-index: 1;
}
.grande  {
    height: 1000px;
}
.font {font-family: 'Poppins', sans-serif;
    font-size: $font-size;
    color: #3a4045;
}
.flex {
    display: flex;
    flex-direction: column;
    
}
.bg-full::before{
    content: "";
    background-color: rgba(0, 0, 0, 0.4);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}